@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Muli", sans-serif;
}

.navbar {
    background-color: #fff !important;
}

.navbar a {
    font-size: 1.3rem;
    text-transform: capitalize;
}

.navbar-brand {
    font-size: 1.9rem !important;
    color: #565387 !important;
}

.navbar a:hover {
    color: #3498db !important;
}

.menu_active {
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

@media screen and (max-width: 991px) {
    .navbar-brand {
        font-size: 1.5rem !important;
    }
    .menu_active {
        font-weight: bold;
        border-bottom: none;
    }
}

/* Main Landing Page */

#header {
    width: 100%;
    height: 80vh;
    margin-top: 70px;
    /* margin-bottom: -10px; */
}

#header h1 {
    margin: 0 0 10px;
    font-size: 48px;
    font-weight: 700;
    color: #000;
}

.brand-name {
    color: #3498db;
}

#header h2 {
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-get-started:hover {
    background: #3498db;
    color: #fff;
}

.header-img {
    text-align: right;
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

/* Services */

.text-center {
    font-size: 30px;
    font-weight: 500;
}

/* Contact */

.contact_div {
    width: 100%;
    height: 75vh;
}

/* Footer */

.footer-center {
    text-align: center;
    position: static;
    bottom: 0;
}

footer p {
    padding: 0;
    margin: 0;
    font-size: 16px;
}

/* responsive code */

@media screen and (max-width: 992px) {
    #header {
        height: 400px;
        text-align: center;
        margin-bottom: 330px;
    }
    #header .header-img {
        text-align: center;
    }
    #header .header-img img {
        width: 60%;
    }
    .header-img {
        margin-top: 260px;
    }
}

@media screen and (max-width: 768px) {
    #header {
        height: 300px;
        margin-top: 20px;
        margin-bottom: 280px;
    }
    #header h1 {
        font-size: 28px;
    }
    #header h2 {
        font-size: 18px;
        margin-bottom: 30px;
    }
    #header .header-img img {
        width: 70%;
    }
}
